import { applyVueInReact } from 'veaury'
import { forwardRef, useRef, useImperativeHandle } from 'react'
// @ts-ignore - Vue component import
import VueDesigner from '../components/Designer.vue'

/**
 * React wrapper for Vue Designer component using veaury
 * 
 * This component bridges the Vue Designer component to React applications.
 * It maintains all the functionality of the original Vue component while
 * providing a React-compatible interface.
 */

// Define props interface matching the Vue component
export interface DesignerConfig {
  /** Container element or selector */
  container?: HTMLElement | string
  
  /** UEditor path */
  ueditorPath?: string
  
  /** UEditor config */
  ueditorConfig?: Record<string, any>
  
  /** Category loader */
  categoryLoader?: () => Promise<any[]>
  
  /** Style loader */
  styleLoader?: (params?: any) => Promise<any>
  
  /** Ready callback */
  onReady?: () => void
  
  /** Change callback */
  onChange?: (content: string) => void
}

export interface DesignerProps {
  /** Designer configuration */
  config?: DesignerConfig
  
  /** Whether in iframe mode */
  isIframe?: boolean
  
  /** Ready event handler */
  onReady?: () => void
  
  /** Change event handler */
  onChange?: (content: string) => void
  
  /** Confirm event handler (iframe mode) */
  onConfirm?: (content: string) => void
}

export interface DesignerRef {
  /** Get editor content */
  getContent: () => string
  
  /** Set editor content */
  setContent: (content: string) => void
}

/**
 * UEditor Plus Designer component for React
 * 
 * A visual content design tool based on UEditor, wrapped for React applications.
 * 
 * @example
 * ```tsx
 * import { UEditorPlusDesigner } from './UEditorPlusDesigner'
 * 
 * function App() {
 *   const designerRef = useRef(null)
 *   
 *   const config = {
 *     ueditorPath: '/ueditor-plus',
 *     ueditorConfig: {}
 *   }
 *   
 *   const handleReady = () => {
 *     console.log('Designer ready')
 *     // Access methods through ref
 *     const content = designerRef.current.getContent()
 *     console.log('Content:', content)
 *   }
 *   
 *   return (
 *     <UEditorPlusDesigner
 *       ref={designerRef}
 *       config={config}
 *       onReady={handleReady}
 *     />
 *   )
 * }
 * ```
 */

// First, wrap the Vue component with veaury
// @ts-ignore - veaury type compatibility
const VueDesignerReact = applyVueInReact(VueDesigner)

// Then create a forwarding wrapper to properly expose methods
export const UEditorPlusDesigner = forwardRef((props: any, ref) => {
  const vueComponentRef = useRef<any>(null)
  
  // Expose methods to parent component
  useImperativeHandle(ref, () => ({
    getContent: () => {
      // Try different access patterns for Veaury wrapped components
      const vueRef = vueComponentRef.current
      if (!vueRef) return ''
      
      // Pattern 0: Veaury special property - Access Vue instance through __veauryVueRef__
      if (vueRef.__veauryVueRef__ && typeof vueRef.__veauryVueRef__.getContent === 'function') {
        return vueRef.__veauryVueRef__.getContent()
      }
      
      // Pattern 1: Direct access
      if (typeof vueRef.getContent === 'function') {
        return vueRef.getContent()
      }
      
      // Pattern 2: Through $refs
      if (vueRef.$refs && vueRef.$refs.component && typeof vueRef.$refs.component.getContent === 'function') {
        return vueRef.$refs.component.getContent()
      }
      
      // Pattern 3: Through exposed
      if (vueRef.exposed && typeof vueRef.exposed.getContent === 'function') {
        return vueRef.exposed.getContent()
      }
      
      // Pattern 4: Through $exposed  
      if (vueRef.$exposed && typeof vueRef.$exposed.getContent === 'function') {
        return vueRef.$exposed.getContent()
      }
      
      console.error('Cannot find getContent method in:', vueRef)
      console.error('Available properties:', Object.keys(vueRef))
      return ''
    },
    setContent: (content: string) => {
      const vueRef = vueComponentRef.current
      if (!vueRef) return
      
      // Pattern 0: Veaury special property - Access Vue instance through __veauryVueRef__
      if (vueRef.__veauryVueRef__ && typeof vueRef.__veauryVueRef__.setContent === 'function') {
        vueRef.__veauryVueRef__.setContent(content)
        return
      }
      
      // Try different access patterns
      if (typeof vueRef.setContent === 'function') {
        vueRef.setContent(content)
      } else if (vueRef.$refs && vueRef.$refs.component && typeof vueRef.$refs.component.setContent === 'function') {
        vueRef.$refs.component.setContent(content)
      } else if (vueRef.exposed && typeof vueRef.exposed.setContent === 'function') {
        vueRef.exposed.setContent(content)
      } else if (vueRef.$exposed && typeof vueRef.$exposed.setContent === 'function') {
        vueRef.$exposed.setContent(content)
      } else {
        console.error('Cannot find setContent method in:', vueRef)
        console.error('Available properties:', Object.keys(vueRef))
      }
    }
  }))
  
  // @ts-ignore
  return <VueDesignerReact ref={vueComponentRef} {...props} />
})

// Export with default for convenience
export default UEditorPlusDesigner
